import axios from "axios";
import { useFormik } from "formik";
import * as Yup from "yup";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import register from '../../assets/ecommerce2.png'
import style from "./Register.module.css";

export default function Register() {
  const [error, setError] = useState("");
  const [loading, setLodaing] = useState(false);
  let validationSchema = Yup.object({
    name: Yup.string()
      .required("* name is required")
      .min(3, "minimum name 3 char ")
      .max(15, "maxmum name 15 char"),
    email: Yup.string().required("* email is required").email("email invalid"),
    password: Yup.string()
      .required("* passowrd is required")
      .matches(/^[A-Z][a-z0-9]{5,10}$/, "password must start with UpperCase"),
    rePassword: Yup.string()
      .required("*confirm password is required")
      .oneOf([Yup.ref("password")], "* rePassword not match"),
    phone: Yup.string()
      .required("* phone is required")
      .matches(/^01[0125][0-9]{8}$/, " phone not match"),
  });
  let formik = useFormik({
    initialValues: {
      name: "",
      email: "",
      password: "",
      rePassword: "",
      phone: "",
    },
    validationSchema,
    onSubmit: sendRegisterData,
  });
  let navigate = useNavigate();
  async function sendRegisterData(values) {
    setLodaing(true);
    let { data } = await axios
      .post("https://route-ecommerce.onrender.com/api/v1/auth/signup", values)
      .catch((error) => {
        setLodaing(false);
        setError(
          `${error.response.data.errors.param} : ${error.response.data.errors.msg}`
        );
      });
    if (data.message === "success") {
      setLodaing(false);
      navigate("/login");
    }

  }

  return (
    <>
      <div className="container">
        <div className="row py-4 flex-column-reverse flex-lg-row">
          <div className="col-lg-6 col-sm-12 py-5">
            <div className="image text-center">
            <img src={register} className='w-75' alt="" />

            </div>
          </div>
          <div className="col-lg-6 col-sm-12 py-5">
            <div className="titleRegister text-center">
              <h2 className="text-main text-capitalize fw-bold">create account</h2>
            </div>
            <div className="contentRegister">
              <form onSubmit={formik.handleSubmit}>
                {error ? (
                  <div className="alertRegister text-danger ">{error}</div>
                ) : (
                  ""
                )}
                <div className="contentInput">
                  <div className="row flex-nowrap">
                    <div className="col-sm-1 icon">
                      <i className="fa-solid fa-user "></i>
                    </div>
                    <div className="col-sm-11 input">
                      <input
                        type="text"
                        name="name"
                        id="name"
                        className=" "
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                        value={formik.values.name}
                        placeholder='userName'
                      />
                      {formik.errors.name && formik.touched.name ? (
                        <div className="alertRegister text-danger ">
                          {formik.errors.name}
                        </div>
                      ) : (
                        ""
                      )}
                    </div>
                  </div>
                </div>
                <div className="contentInput">
                  <div className="row flex-nowrap">
                    <div className="col-sm-1 icon">
                      <i className="fa-solid fa-at "></i>
                    </div>
                    <div className="col-sm-11 input">
                      <input
                        type="email"
                        name="email"
                        id="email"
                        className=" "
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                        value={formik.values.email}
                        placeholder='userEmail'
                      />
                      {formik.errors.email && formik.touched.email ? (
                        <div className="alertRegister text-danger ">
                          {formik.errors.email}
                        </div>
                      ) : (
                        ""
                      )}
                    </div>
                  </div>
                </div>
                <div className="contentInput">
                  <div className="row flex-nowrap">
                    <div className="col-sm-1 icon">
                      <i className="fa-solid fa-lock "></i>
                    </div>
                    <div className="col-sm-11 input">
                      <input
                        type="password"
                        name="password"
                        id="password"
                        className=" "
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                        value={formik.values.password}
                        placeholder='password'
                      />
                      {formik.errors.password && formik.touched.password ? (
                        <div className="alertRegister text-danger ">
                          {formik.errors.password}
                        </div>
                      ) : (
                        ""
                      )}
                    </div>
                  </div>
                </div>
                <div className="contentInput">
                  <div className="row flex-nowrap">
                    <div className="col-sm-1 icon">
                      <i className="fa-solid fa-lock "></i>
                    </div>
                    <div className="col-sm-11 input">
                      <input
                        type="password"
                        name="rePassword"
                        id="rePassword"
                        className=" "
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                        value={formik.values.rePassword}
                        placeholder='confirm Passowrd'
                      />
                      {formik.errors.rePassword && formik.touched.rePassword ? (
                        <div className="alertRegister text-danger ">
                          {formik.errors.rePassword}
                        </div>
                      ) : (
                        ""
                      )}
                    </div>
                  </div>
                </div>
                <div className="contentInput">
                  <div className="row flex-nowrap">
                    <div className="col-sm-1 icon">
                      <i className="fa-solid fa-phone "></i>
                    </div>
                    <div className="col-sm-11 input">
                      <input
                        type="tel"
                        name="phone"
                        id="phone"
                        className=" "
                        onChange={formik.handleChange}
                        onBlur={formik.handleBlur}
                        value={formik.values.phone}
                        placeholder='phone'
                      />
                      {formik.errors.phone && formik.touched.phone ? (
                        <div className="alertRegister text-danger ">
                          {formik.errors.phone}
                        </div>
                      ) : (
                        ""
                      )}
                    </div>
                  </div>
                </div>
                <div className="buttonRegister text-center" >
                {loading ? (
                  <button
                    type="button"
                    className="btn bg-main text-white  mt-3 px-5 rounded-pill"
                  >
                    <i className="fa-solid fa-circle-notch fa-spin text-white  " ></i>

                  </button>
                ) : (
                  <button
                    disabled={!(formik.isValid && formik.dirty)}
                    type="submit"
                    className=" btn bg-main text-white  mt-3 text-uppercase rounded-pill px-5"
                  >
                    Register
                  </button>
                )}
                </div>

              </form>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
